html {
  min-width: min-content;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: relative;
  min-width: 1024px;
  background-color: var(--body-light-bg);
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}
a:focus-visible {
  outline: 5px auto var(--card-link);
  outline-offset: 2px;
}

ul {
  list-style: none;
}

ol {
  list-style: none;
}

.page-container {
  max-width: 1500px;
  margin: 0 auto;
}

.product-container {
  max-width: 1500px;
  margin: 0 auto;
  background-color: var(--light-bg);
}

.card-container-1 {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  z-index: 200;
  margin-top: -350px;
  padding: 0 10px;
}

.card-container-2 {
  width: 100%;
  position: relative;
  padding: 0 20px;
}

.card-container-3 {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  padding: 0 10px;
  overflow: hidden;
  height: 420px;
}

.card-container-4 {
  width: 100%;
  position: relative;
  padding: 0 20px;
}

:root {
  --body-light-bg:hsl(180, 8%, 92%);
  --nav-dark: hsl(214, 27%, 10%);
  --nav2-dark:hsl(213, 28%, 19%);
  --nav3-links:hsl(0,0%,20%);
  --text-light: hsl(0,0%,100%);
  --light-bg: hsl(0,0%,100%);
  --text-less-light: hsl(0, 0%, 80%);
  --categories-label-bg: hsl(0, 0%, 95%);
  --categories-label: hsl(0, 0%, 33%);
  --categories-label-bg-active: hsl(0, 0%, 90%);
  --accent:hsl(34, 99%, 70%);
  --search-button-hover:hsl(34, 88%, 62%);
  --accent-active:hsl(32, 88%, 50%);
  --down-arrow:hsl(213, 7%, 68%);
  --cart-count:hsl(34,97%,48%);
  --card-bg: hsl(0,0%,100%);
  --card-text-small: hsl(180,6%,6%);
  --card-link:hsl(189,100%,26%);
  --card-signin-button:hsl(50,100%,54%);
  --card-signin-button-active:hsl(50,100%,48%);
  --card-wide-header:hsl(0,0%,27%);
  --card-scrollbar-thumb:hsl(0, 0%, 67%);
  --card-scrollbar-thumb-hover:hsl(0, 0%, 53%);
  --footer-bg: hsl(213,28%, 19%);
  --footer-bg-bottom:hsl(212, 28%, 10%);
  --back-to-top-bg:hsl(213, 24%, 28%);
  --back-to-top-bg-hover:hsl(213, 19%, 35%);
  --bottom-links-secondary:hsl(0,0%,60%);
  --bottom-links-primary:hsl(0,0%,87%);
  --thumbnail-border:hsl(216,6%,65%);
  --thumbnail-border-active:hsl(31,100%,45%);
  --section-divider:hsl(0deg 0% 0% / 10%);
  --dark-orange-text: hsl(12, 96%, 35%);
  --time-limit:hsl(180, 2%, 34%);
  --left-in-stock:hsl(120, 100%, 23%);
  --btn-buy-now:hsl(36, 100%, 55%);
  --btn-buy-now-hover:hsl(33, 100%, 49%);
}

:root {
  --fs-300: 0.75rem;
  --fs-350: 0.8125rem;
  --fs-400: 0.875rem;
  --fs-500: 1rem;
  --fs-550: 1.125rem;
  --fs-600: 1.3125rem;
  --fs-700: 1.5rem;
}

body {
  font-family: "Ubuntu", sans-serif;
}

option {
  font-family: "Ubuntu", sans-serif;
}

h1 {
  font-size: var(--fs-700);
}

h2 {
  font-size: var(--fs-600);
}

.nav-item {
  height: 3.125rem;
  padding: 0 0.375rem 0 0.375rem;
}
.nav-item:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px var(--accent-active);
}

.flex {
  display: flex;
  align-items: center;
}

.nav-text {
  color: var(--text-light);
  font-weight: 700;
  padding-left: 3px;
  font-size: var(--fs-400);
  line-height: 1.2;
  flex-shrink: 0;
  white-space: nowrap;
}
.nav-text::first-line {
  color: var(--text-less-light);
  font-size: var(--fs-300);
  font-weight: 400;
}

.nav-down-arrow {
  display: inline-block;
  width: 0;
  height: 0;
  border-width: 4px;
  border-style: solid;
  border-color: transparent;
  border-top-color: var(--down-arrow);
  border-bottom-width: 0;
  margin: 16px 0 0 2px;
}
.nav-down-arrow--prime {
  margin: 0 0 2px 0;
}

.popUp-up-arrow {
  display: inline-block;
  position: absolute;
  top: -8px;
  left: 50px;
  width: 0;
  height: 0;
  border-width: 8px;
  border-style: solid;
  border-color: transparent;
  border-bottom-color: var(--light-bg);
  border-top-width: 0;
}

.nav-icon {
  background: url(../assets/icons/icons.png);
}

.nav-focus {
  box-shadow: 0 0 0 3px var(--accent-active);
}

.nav-hover > :not(:nth-child(3)):hover {
  border-radius: 1px;
  box-shadow: 0 0 0 1px var(--text-light);
}

.flag-icon {
  background: url(../assets/icons/flags.png);
}

.nav-wrapper {
  width: 100%;
  min-width: min-content;
}

.nav {
  position: relative;
  height: 3.75rem;
  padding: 0 0.625rem 0 0.75rem;
  background-color: var(--nav-dark);
  width: 100%;
  z-index: 500;
}
.nav__logo_img {
  display: inline-block;
  height: 1.875rem;
  width: 6.0625rem;
  margin-top: 8px;
  background-position: -10px -51px;
}
.nav__logo_text {
  font-size: var(--fs-400);
  color: var(--text-light);
  margin: -4px 2px 0 0;
}
.nav__location {
  padding: 0 9px;
}
.nav__location_logo {
  height: 1.125rem;
  width: 15px;
  margin-top: 0.625rem;
  background-position: -71px -378px;
  flex-shrink: 0;
}
.nav__search {
  width: auto;
  flex-grow: 1;
  height: 2.5rem;
  background-color: var(--light-bg);
  border-radius: 0.25rem;
  margin: 0 0.9375rem 0 0.9375rem;
}
.nav__search_categories {
  height: 2.5rem;
  width: max-content;
  padding: 0 0.625rem;
  border-radius: 0.25rem 0 0 0.25rem;
  outline: none;
  border-right: 1px solid var(--text-less-light);
  background-color: var(--categories-label-bg);
  z-index: 100;
  position: relative;
  overflow: hidden;
}
.nav__search_categories:focus-within {
  box-shadow: 0 0 0 3px var(--accent-active);
}
.nav__search_categories:hover {
  background-color: var(--categories-label-bg-active);
}
.nav__search_categories_selected_label {
  font-size: var(--fs-300);
  color: var(--categories-label);
}
.nav__search_categories_selected_down_arrow {
  margin: 0 0 0 10px;
  border-top-color: #666;
}
.nav__search_categories_list {
  height: 40px;
  opacity: 0;
  position: absolute;
  left: 0;
  width: auto;
  cursor: pointer;
}
.nav__search_text {
  border: none;
  outline: none;
  font-size: 18px;
  flex-grow: 1;
  z-index: 99;
  padding: 7px;
}
.nav__search_button {
  background-color: var(--accent);
  background-position: 2px -280px;
  outline: none;
  border: none;
  width: 45px;
  height: 40px;
  z-index: 100;
  border-radius: 0 0.25rem 0.25rem 0;
  cursor: pointer;
}
.nav__search_button:hover {
  background-color: var(--search-button-hover);
}
.nav__search_button:focus {
  box-shadow: 0 0 0 3px var(--accent-active);
}
.nav__language {
  width: auto;
  padding: 9px;
}
.nav__language_flag {
  display: inline-block;
  width: 24px;
  height: 18px;
  background-position: 0 -166px;
  margin-top: 10px;
}
.nav__language_down_arrow {
  margin-left: 5px;
}
.nav__signin_text {
  margin-right: 5px;
}
.nav__signin_text::first-line {
  color: var(--text-light);
}
.nav__signin_down_arrow {
  margin-right: 2px;
}
.nav__orders_text {
  padding: 0;
  margin: 0 2px;
}
.nav__orders_text::first-line {
  color: var(--text-light);
}
.nav__cart_icon {
  height: 26px;
  width: 38px;
  background-position: -10px -340px;
  position: relative;
  margin-left: 2px;
}
@media screen and (max-width: 1024px) {
  .nav__cart_icon {
    margin-right: 2px;
  }
}
.nav__cart_count {
  position: absolute;
  color: var(--cart-count);
  font-weight: 700;
  font-size: var(--fs-500);
  top: -5px;
  left: 17px;
}
.nav__cart_text {
  color: var(--text-light);
  font-weight: 700;
  font-size: var(--fs-400);
  margin-top: 10px;
  margin-right: 2px;
}
@media screen and (max-width: 1024px) {
  .nav__cart_text {
    display: none;
  }
}

.nav2 {
  height: 40px;
  background-color: var(--nav2-dark);
  padding: 0 0.9375rem 0 0.625rem;
}
.nav2__side_menu {
  padding: 7px 9px;
  outline: none;
}
.nav2__side_menu:focus-visible {
  box-shadow: 0 0 0 3px var(--accent-active);
}
.nav2__side_menu:hover {
  border-radius: 1px;
  box-shadow: 0 0 0 1px var(--text-light);
}
.nav2__side_menu_icon {
  width: 17px;
  height: 14px;
  background-position: -172px -255px;
}
.nav2__side_menu_text {
  color: var(--text-light);
  font-size: var(--fs-400);
  font-weight: 700;
  margin-left: 5px;
}
.nav2__menu_list {
  height: 100%;
  flex-wrap: wrap;
  flex: 1 1 auto;
  padding-left: 3px;
}
.nav2__menu_list_link {
  display: inline-block;
  color: var(--text-light);
  font-size: var(--fs-400);
  padding: 6px 9px;
  margin: 5px 0 6px 0;
  white-space: nowrap;
  position: relative;
}
.nav2__menu_list_link--prime:hover > .primePopUp {
  display: block;
}
.nav2__menu_list_link:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px var(--accent-active);
}
.nav2__menu_list_link:hover {
  border-radius: 1px;
  box-shadow: 0 0 0 1px var(--text-light);
}

.nav3 {
  height: 50px;
  width: 100%;
  justify-content: flex-start;
  padding: 0 10px 0 30px;
  background-color: var(--light-bg);
  border-bottom: 1px solid var(--section-divider);
}
.nav3__menu_list_link {
  font-size: var(--fs-300);
  color: var(--nav3-links);
  white-space: nowrap;
  height: 100%;
  padding-inline: 10px;
  outline: none;
}
.nav3__menu_list_link:focus-visible {
  outline: none;
  box-shadow: inset 0 0 0 3px var(--accent-active);
}
.nav3__menu_list_link span {
  display: block;
  margin-top: 18px;
}
.nav3__menu_list_link--active {
  font-size: var(--fs-400);
  font-weight: 700;
}
.nav3__menu_list_link--active span {
  line-height: 0.9;
}
.nav3__menu_list_link--active:hover > span {
  box-shadow: none !important;
}
.nav3__menu_list_link:hover > span {
  box-shadow: 0 2px 0 0 var(--accent-active);
}

.primePopUp {
  display: none;
  position: absolute;
  width: 320px;
  aspect-ratio: 1;
  border-radius: 3px;
  background-color: var(--light-bg);
  top: 27px;
  left: -4px;
  z-index: 9999;
  padding: 15px;
}
.primePopUp img {
  width: 100%;
  height: 100%;
}

.carousel {
  width: 100%;
  height: 600px;
  position: relative;
  overflow: hidden;
}
.carousel::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, transparent 30%, var(--body-light-bg));
  z-index: 100;
}
.carousel__button {
  position: absolute;
  top: 0;
  height: 250px;
  width: 80px;
  background-color: transparent;
  z-index: 100;
  border-radius: 5px;
  outline: none;
  border: none;
}
.carousel__button:focus {
  box-shadow: inset 0 0 0 2px var(--light-bg), inset 0 0 0 4px #008094;
}
.carousel__button span {
  display: block;
  width: 34px;
  height: 52px;
  margin: auto;
  background: url(../assets/icons/carousel-arrow.png);
  background-repeat: no-repeat;
  background-position: 0 center;
  background-size: cover;
}
.carousel__button--prev {
  left: 0;
}
.carousel__button--next {
  right: 0;
}
.carousel__button--next span {
  background-position: right center;
}
.carousel__track {
  height: 100%;
  position: relative;
  transition: all 0.5s ease-in;
}
.carousel__track_slide {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}
.carousel__track_slide_img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1 0 280px;
  height: 420px;
  max-width: 400px;
  min-width: 280px;
  padding: 20px;
  margin: 0 10px;
  background-color: var(--card-bg);
}
.card--signin {
  height: max-content;
}
.card--signin .card__bottom {
  position: static;
  padding: 0;
  margin: 5px 0 0 0;
  border-radius: 8px;
  overflow: hidden;
}
.card--signin .card__bottom a {
  display: block;
  background-color: var(--card-signin-button);
  width: 100%;
  padding: 10px;
  text-align: center;
  color: var(--card-text-small);
}
.card--signin .card__bottom a:hover {
  background-color: var(--card-signin-button-active);
}
.card--single .card__content {
  display: block;
}
.card--single .card__link img {
  width: 100%;
  height: calc(100% - 10px);
  object-fit: cover;
}
.card-divider {
  width: 100%;
  height: 20px;
  border: none;
}
.card-divider--2 {
  display: none;
}
.card__header {
  margin-bottom: 10px;
}
.card__header h2 {
  color: var(--card-text-small);
  line-height: 1.3;
}
.card__content {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  width: 100%;
  margin-bottom: 45px;
  flex-grow: 1;
}
.card__link {
  min-height: 130.2px;
}
.card__link img {
  width: 100%;
  height: 80%;
  object-fit: cover;
}
.card__link span {
  display: block;
  font-size: var(--fs-300);
  color: var(--card-text-small);
}
.card__bottom {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  padding: 0 20px;
  margin-bottom: 20px;
}
.card__bottom a {
  font-size: 13px;
  color: var(--card-link);
}

.card-wide {
  width: 100%;
  height: auto;
  padding: 20px;
  background-color: var(--card-bg);
}
.card-wide:hover :is(.card-wide__carousel-button, .card-wide__scrollbar-thumb) {
  opacity: 1;
}
.card-wide:not(:hover) .card-wide__carousel-button {
  opacity: 0 !important;
}
.card-wide__header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.card-wide__header h2 {
  color: var(--card-wide-header);
  font-weight: 400;
}
.card-wide__header a {
  margin-left: 20px;
}
.card-wide__carousel {
  width: 100%;
  height: 200px;
  position: relative;
  overflow: hidden;
}
.card-wide__carousel-button {
  opacity: 0;
  position: absolute;
  height: 100px;
  width: 45px;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--card-bg);
  z-index: 100;
  border: none;
  outline: none;
  transition: opacity 0.3s ease-in;
}
.card-wide__carousel-button:focus {
  outline: 2px auto var(--card-link);
  outline-offset: -2px;
}
.card-wide__carousel-button span {
  display: block;
  width: 13px;
  height: 22px;
  margin: auto;
  background: url(../assets/icons/cardMedia.png);
  background-repeat: no-repeat;
}
.card-wide__carousel-button--prev {
  left: 0;
  border-radius: 0 5px 5px 0;
}
.card-wide__carousel-button--prev span {
  background-position: -14px 0;
}
.card-wide__carousel-button--next {
  right: 0;
  border-radius: 5px 0 0 5px;
}
.card-wide__carousel-button--next span {
  background-position: 0 0;
}
.card-wide__carousel-track {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-wrap: nowrap;
  transition: all 0.5s ease-in;
}
.card-wide__slide {
  margin-right: 30px;
}
.card-wide__scrollbar {
  position: relative;
  margin-bottom: -10px;
  margin-top: 20px;
  width: 100%;
  height: 6px;
}
.card-wide__scrollbar-thumb {
  position: absolute;
  content: "";
  width: 300px;
  height: 6px;
  left: 0;
  background-color: var(--card-scrollbar-thumb);
  border-radius: 6px;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease-in;
}
.card-wide__scrollbar-thumb:hover {
  background-color: var(--card-scrollbar-thumb-hover);
}

.footer-wrapper {
  width: 100%;
  min-width: min-content;
  background-color: var(--footer-bg);
}

.footer__backToTop a {
  display: block;
  width: 100%;
  height: 50px;
  font-size: var(--fs-400);
  color: var(--text-light);
  background-color: var(--back-to-top-bg);
  text-align: center;
  line-height: 50px;
}
.footer__backToTop a:hover {
  background-color: var(--back-to-top-bg-hover);
}
.footer__divider {
  border: none;
  border-top: 1px solid var(--back-to-top-bg);
}
.footer__linksSection {
  display: flex;
  gap: 121px;
  margin: 40px auto;
  padding-inline: 10px;
  max-width: 1000px;
}
.footer__linksContainer {
  display: flex;
  flex-direction: column;
}
.footer__linksContainer a {
  color: var(--bottom-links-primary);
  font-size: var(--fs-400);
  margin-bottom: 10px;
}
.footer__linksContainer a:hover {
  text-decoration: underline;
}
.footer__linksHeader {
  font-size: var(--fs-500);
  color: var(--text-light);
  font-weight: 700;
  margin-block: 6px 14px;
}
.footer__language {
  display: flex;
  height: 70px;
  justify-content: center;
  align-items: center;
  margin: 10px auto;
}
.footer__language .homepage {
  cursor: pointer;
  width: 76px;
  height: 23px;
  margin-top: 10px;
  margin-right: 100px;
  background: url(../assets/icons/icons.png);
  background-position: -10px -90px;
}
.footer__language .language {
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 8px 8px;
  border: 1px solid var(--back-to-top-bg-hover);
  border-radius: 3px;
}
.footer__language .language .globeIcon {
  display: inline-block;
  width: 15px;
  height: 15px;
  background: url(../assets/icons/flags.png);
  background-position: -77px -24px;
}
.footer__language .language .txt {
  margin-inline: 10px 20px;
  color: var(--text-less-light);
  font-size: var(--fs-350);
}
.footer__language .language .upDownIcon {
  display: inline-block;
  width: 12px;
  height: 12px;
  background: url(../assets/icons/flags.png);
  background-position: -77px -80px;
}
.footer__regions ul {
  display: block;
  max-width: 1000px;
  margin: 10px auto 30px auto;
  text-align: center;
}
.footer__regions ul li {
  display: inline-block;
}
.footer__regions ul li a {
  color: var(--bottom-links-primary);
  font-size: var(--fs-300);
  padding: 0 7px;
}
.footer__regions ul li a:hover {
  text-decoration: underline;
}
.footer__otherServices {
  background-color: var(--footer-bg-bottom);
  padding: 30px 0;
}
.footer__otherServicesContainer {
  margin: 0 auto;
  display: grid;
  width: min-content;
  padding-inline: 10px;
  grid-template-columns: repeat(5, 1fr);
  row-gap: 20px;
  column-gap: 119px;
  grid-template-rows: 1fr 1fr;
}
.footer__otherServicesContainer a {
  font-size: 10px;
  color: var(--bottom-links-secondary);
  white-space: nowrap;
}
.footer__otherServicesContainer a::first-line {
  font-size: 11px;
  color: var(--bottom-links-primary);
}
.footer__otherServicesContainer a:hover {
  text-decoration: underline;
}
.footer__copyrights {
  background-color: var(--footer-bg-bottom);
  padding-bottom: 30px;
}
.footer__copyrightsContainer {
  margin: 0px auto;
  width: max-content;
}
.footer__copyrightsContainer ul {
  display: inline-block;
  width: max-content;
  margin-left: auto;
}
.footer__copyrightsContainer ul li {
  display: inline-block;
  padding: 0 7px;
}
.footer__copyrightsContainer ul li a {
  font-size: var(--fs-300);
  color: var(--bottom-links-primary);
}
.footer__copyrightsContainer ul li a:hover {
  text-decoration: underline;
}
.footer__copyrightsContainer span {
  font-size: var(--fs-300);
  color: var(--bottom-links-primary);
  padding: 0 7px;
  margin-right: auto;
}

.categoryLabel {
  font-size: var(--fs-300);
  padding: 10px 20px;
}

.product {
  width: 100%;
  height: max-content;
  display: flex;
